var idb = require('idb');

module.exports = {
  save: function save(sightingName, sightingDatetime, carsSighted) {
    var result = idb.open('mydb', 1, function (upgradeDB) {
      upgradeDB.createObjectStore('sightings');
    }).then(function (db) {
      var val = {
        sightingDatetime: sightingDatetime,
        carsSighted: carsSighted
      };
      var key = sightingName;
      var tx = db.transaction('sightings', 'readwrite');
      tx.objectStore('sightings').put(val, key);
      return tx.complete;
    });
    return result;
  },
  getAll: function getAll(source, callback) {
    var dbPromise = idb.open('mydb', 1, function (upgradeDB) {
      upgradeDB.createObjectStore(source);
    });
    dbPromise.then(function (db) {
      var tx = db.transaction(source);
      var store = tx.objectStore(source);
      return store.getAll();
    }).then(function (items) {
      callback(items);
    });
  },
  get: function get(id) {},
  delete: function _delete(id) {}
};