var sighting = require('./src/sighting.js');

var getsightings = module.exports = function (source, db, callback) {
  try {
    var result = sighting.getSightings(source, db, callback);
    var message = '';
  } catch (err) {
    var result = [];
    var message = err.message;
  }

  var data = {
    result: result,
    message: message
  };
  return data;
};